import { useEffect, useState } from 'react';
import AdminGuard from '../../components/AdminGuard';
import { supabase } from '../../lib/supabase';

type KycRecord = {
  id: string;
  email: string;
  kyc_status: 'pending' | 'approved' | 'rejected';
  kyc_level: number;
  kyc_submitted_at: string | null;
};

export default function AdminKyc() {
  const [list, setList] = useState<KycRecord[]>([]);
  const [loading, setLoading] = useState(true);
  const [msg, setMsg] = useState('');

  async function fetchKyc() {
    setLoading(true);
    const { data, error } = await supabase
      .from('profiles')
      .select('id,email,kyc_status,kyc_level,kyc_submitted_at')
      .order('kyc_submitted_at', { ascending: false });
    if (error) console.error(error);
    else setList((data as any) || []);
    setLoading(false);
  }

  async function setStatus(userId: string, status: 'approved' | 'rejected') {
    const { data: session } = await supabase.auth.getSession();
    const token = session.session?.access_token;
    if (!token) return setMsg('Session lost');
    try {
      const res = await fetch('/api/admin/kyc', {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${token}` },
        body: JSON.stringify({ userId, status }),
      });
      const json = await res.json();
      if (!res.ok) throw new Error(json.error || 'Failed');
      setMsg(`KYC ${status}`);
      fetchKyc();
    } catch (e: any) {
      setMsg(e.message);
    }
  }

  useEffect(() => {
    fetchKyc();
  }, []);

  return (
    <AdminGuard>
      <div className="max-w-5xl mx-auto p-6">
        <h1 className="text-2xl font-semibold mb-4">KYC Review</h1>
        {msg && <p className="mb-2 text-sm text-blue-600">{msg}</p>}
        {loading && <p className="text-sm">Loading…</p>}
        <div className="overflow-auto">
          <table className="min-w-full table-auto text-sm">
            <thead>
              <tr className="bg-gray-100 text-black">
                <th className="px-2 py-1 text-left">Email</th>
                <th className="px-2 py-1 text-left">Level</th>
                <th className="px-2 py-1 text-left">Status</th>
                <th className="px-2 py-1 text-left">Submitted</th>
                <th className="px-2 py-1">Actions</th>
              </tr>
            </thead>
            <tbody>
              {list.map((u) => (
                <tr key={u.id} className="border-t">
                  <td className="px-2 py-1">{u.email}</td>
                  <td className="px-2 py-1">{u.kyc_level}</td>
                  <td className="px-2 py-1">
                    <span
                      className={`inline-block px-2 rounded text-xs ${
                        u.kyc_status === 'approved'
                          ? 'bg-green-100 text-green-800'
                          : u.kyc_status === 'rejected'
                          ? 'bg-red-100 text-red-800'
                          : 'bg-yellow-100 text-yellow-800'
                      }`}
                    >
                      {u.kyc_status}
                    </span>
                  </td>
                  <td className="px-2 py-1">{u.kyc_submitted_at ? new Date(u.kyc_submitted_at).toLocaleString() : '-'}</td>
                  <td className="px-2 py-1">
                    {u.kyc_status === 'pending' && (
                      <div className="flex gap-2">
                        <button
                          onClick={() => setStatus(u.id, 'approved')}
                          className="text-xs bg-green-600 text-white px-2 py-1 rounded"
                        >
                          Approve
                        </button>
                        <button
                          onClick={() => setStatus(u.id, 'rejected')}
                          className="text-xs bg-red-600 text-white px-2 py-1 rounded"
                        >
                          Reject
                        </button>
                      </div>
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </AdminGuard>
  );
}
